.home {
    display: flex;
    flex-wrap: wrap;
    input[type=checkbox] {
        margin: 10px;
        height: 20px;
        width: 20px;
        cursor: pointer;
    }

    &-box {
        position: relative;
        padding-right: 20px;  
        height: 10rem;
        text-align: center;  
        &-table {
            margin: auto;    
        }
    }
}